import React, { Fragment, useState, useEffect } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Container from '@material-ui/core/Container'
import Chip from '@material-ui/core/Chip';
import Typography from '@material-ui/core/Typography';
import clsx from 'clsx'
import Autocomplete from '@material-ui/lab/Autocomplete';
import { Button } from '@material-ui/core'
import { DropzoneArea } from 'material-ui-dropzone'

import PropTypes from "prop-types"
import TextField from "@material-ui/core/TextField"
import { createUser, updateUser, getRoles } from '../../api/api';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />
}

inputComponent.propTypes = {
  inputRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({
      current: PropTypes.any.isRequired
    })
  ])
}
const PersonForm = props => {


  useEffect(() => {
    console.log(props)

    getRoles().then(response => {
      setRoles(response.roles);
    })

    if (props.person) {

      setName(props.person.name);
      setDocumentNumber(props.person.document_number);
      setUsername(props.person.username);
      setRole(props.person.role);
    }
  }, [])

  const [dataResponse, setDataResponse] = useState([])
  const [disabled, setDisabled] = useState(false)

  const [file, setFile] = useState({})
  const classes = useStyles()


  const [roles, setRoles] = useState([]);
  const [name, setName] = useState('');
  const [documentNumber, setDocumentNumber] = useState('');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [repeatPassword, setRepeatPassword] = useState('');
  const [role, setRole] = useState(null);

  const [errorName, setErrorName] = useState(false)
  const [errorTextName, setErrorTextName] = useState('')

  const [errorDocument, setErrorDocument] = useState(false)
  const [errorTextDocument, setErrorTextDocument] = useState('')

  const handlePicture = file => {
    setFile(file[0])
  }
  const onChangeName = e => {
    setName(e.target.value);
    validateName(e.target.value);
  }
  const onChangeDocumentNumber = e => {
    setDocumentNumber(e.target.value);
    validateDocument(e.target.value);
  }
  const onChangeUserame = e => {
    setUsername(e.target.value)
  }
  const onChangePassword = e => {
    setPassword(e.target.value)
  }
  const onChangeRepeatPassword = e => {
    setRepeatPassword(e.target.value)
  }
  const validateForm = () => {
    let errors = 0
    errors += validateName()
    // errors += validatePrice()
    // errors += validateQuantity()
    return errors
  }
  const validateName = (value) => {
    let text = value !== undefined ? value : name

    if (text == null || text.toString().trim() === '') {
      setErrorName(true)
      setErrorTextName('Este campo es requerido')
      return 1
    }
    else {
      setErrorName(false)
      setErrorTextName('')
      return 0
    }
  }
  const validateDocument = (value) => {
    let text = value !== undefined ? value : documentNumber

    if (text == null || text.toString().trim() === '') {
      setErrorDocument(true)
      setErrorTextDocument('Este campo es requerido')
      return 1
    }
    else {
      setErrorDocument(false)
      setErrorTextDocument('')
      return 0
    }
  }

  const onSubmitPerson = _ => {

    let errores = validateForm()
    errores += validateDocument();

    if (password && repeatPassword) {
      if (password !== repeatPassword) {
        errores++;
        toast.error('Las contraseñas no son iguales', {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    }



    if (errores === 0) {
      const formData = new FormData();
      if (props.person) {
        formData.append("id", props.person._id);
      }
      formData.append("photo", file);
      formData.append("name", name);
      formData.append("username", username);
      formData.append("documentNumber", documentNumber);
      formData.append("role", role._id);
      formData.append("password", password);

      if (!props.person) {
        createUser(formData).then(response => {
          console.log('response', response);
          if (response.status == 'success') {
            toast.info('Funcionario registrado exitosamente', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
            props.onCloseModal(true);
            // props.onUpdateUsers();
          } else {
            toast.error(response.message, {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }

        });

      } else {

        updateUser(formData).then(response => {
          console.log('response', response);
          if (response.status == 'success') {
            toast.info('Funcionario registrado exitosamente', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          } else {
            toast.error('Ocurrió un error al registrar el funcionario', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }

        });
      }
    } else {
      toast.error('🦄 Wow so easy!', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  }


  return (
    <Fragment>
      <ToastContainer />
      <Container maxWidth="md">

        <Paper className={classes.paper}>
          <form encType={'multipart/form-data'}
            onSubmit={e => {
              e.preventDefault()
              onSubmitPerson()
            }} >
            <Grid container spacing={3}>

              <Grid item xs={12} sm={4}>

                <Autocomplete
                  fullWidth
                  size="small"
                  getOptionLabel={
                    (option) => {
                      return `${option.name.toString().toUpperCase()} `
                    }
                  }
                  filterOptions={(x) => x}
                  options={roles}
                  autoComplete
                  getOptionSelected={(option, value) => option === value}
                  onChange={(_, value) => {
                    setRole(value);
                  }}
                  onInputChange={(_, newInputValue) => {
                    if (newInputValue.toString().trim().length > 0) {
                      let data = {
                        search: newInputValue.toString().trim()
                      }
                      getRoles(data).then(response => {
                        setRoles(response.roles);
                      })
                    }
                  }}
                  renderInput={(params) => (
                    <TextField {...params} label="Roles" variant="outlined" margin="dense" />
                  )}
                  renderTags={(tagValue, getTagProps) =>
                    tagValue.map((option, index) => (
                      <Chip
                        label={option.name}
                        {...getTagProps({ index })}
                        disabled={roles.indexOf(option) !== -1}
                      />
                    ))
                  }
                  renderOption={(option) => {

                    return (
                      <Grid container alignItems="center">
                        <Grid item xs>
                          <Typography >{`${option.name.toString().toUpperCase()}`}</Typography>
                        </Grid>
                      </Grid>
                    );
                  }}
                />
              </Grid>

              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  error={errorName}
                  helperText={errorTextName}
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Nombre"
                  value={name}
                  onChange={onChangeName} />
              </Grid>

              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  error={errorDocument}
                  helperText={errorTextDocument}
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  value={documentNumber}
                  onChange={onChangeDocumentNumber}
                  label="Documento" />
              </Grid>

              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Nombre de usuario"
                  value={username}
                  onChange={onChangeUserame} />
              </Grid>


              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  type='password'
                  label="Contraseña"
                  onChange={onChangePassword} />
              </Grid>

              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  margin="dense"
                  autoComplete='off'
                  type='password'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Repite la contraseña"
                  onChange={onChangeRepeatPassword} />
              </Grid>



              <Grid item xs={12} sm={12}>
                <DropzoneArea
                  open={true}
                  filesLimit={1}
                  dropzoneText="Arrastre y suelte un archivo de imagen aquí o haga clic"
                  acceptedFiles={['image/jpeg', 'image/png', 'image/bmp']}
                  onChange={handlePicture} />

              </Grid>


              <Grid item xs={2} sm={3}>
                <Button disabled={disabled} type="submit" variant="contained" color='primary'>
                  {props.person ? "Actualizar" : "Guardar"}
                </Button>
              </Grid>
            </Grid>
          </form>
        </Paper>

      </Container>
    </Fragment>
  )
}

export default PersonForm