import React, { useState } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles';
import EditIcon from '@material-ui/icons/Edit';
import LockOpenIcon from '@material-ui/icons/LockOpenOutlined';
import PersonIcon from '@material-ui/icons/PersonOutline';

import { Label } from 'components';


import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Button,
  Divider,
  Table,
  TableBody,
  TableRow,
  TableCell,
  colors
} from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

const PersonDetail = props => {

  console.log('props', props)

  const classes = useStyles()

  const [openEdit, setOpenEdit] = useState(false);

  const handleEditOpen = () => {
    setOpenEdit(true);
  };

  const handleEditClose = () => {
    setOpenEdit(false);
  };

  return (
    <Card>
      <CardHeader title="Detalle Usuario" />
      <Divider />
      <CardContent className={classes.content}>
        <Table>
          <TableBody>
            <TableRow>
              <TableCell>Nombres</TableCell>
              <TableCell>{props.person.names}</TableCell>
            </TableRow>
            <TableRow selected>
              <TableCell>Apellidos</TableCell>
              <TableCell>{props.person.last_name}</TableCell>
            </TableRow>
            
            <TableRow>
              <TableCell>Tipo de documento</TableCell>
              <TableCell>{props.person.documentType ? props.person.documentType.documentType : ''}</TableCell>
            </TableRow>

            <TableRow selected>
              <TableCell>Número de documento</TableCell>
              <TableCell>{props.person.documentNumber} {props.person.checkDigit >= 0 ? props.person.checkDigit : ''}</TableCell>
            </TableRow>

            
            <TableRow>
              <TableCell>Correo electrónico</TableCell>
              <TableCell>{props.person.email}</TableCell>
            </TableRow>

            <TableRow selected>
              <TableCell>Dirección</TableCell>
              <TableCell>{props.person.address}</TableCell>
            </TableRow>

            <TableRow >
              <TableCell>Correo electrónico</TableCell>
              <TableCell>{props.person.email}</TableCell>
            </TableRow>

          </TableBody>
        </Table>
      </CardContent>

    </Card>
  )
}

export default PersonDetail