import React, { useState, useEffect, Fragment } from 'react'

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import PersonDetail from './PersonDetail'
import { Button } from '@material-ui/core';

import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import SearchIcon from '@material-ui/icons/Search';
import Slide from '@material-ui/core/Slide';
import RestoreIcon from '@material-ui/icons/Restore';
import PersonForm from "./../Person/PersonForm";

import Tooltip from '@material-ui/core/Tooltip'
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import TablePagination from '@material-ui/core/TablePagination';

import { withStyles } from '@material-ui/core/styles';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { deleteUser, restoreUser, listUsers } from '../../api/api';
import { ToastContainer, toast } from 'react-toastify';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})



const Person = _ => {

    useEffect(() => {
        let data = { start: 0, end: rowsPerPage, paginate: true }

        listUsers(data).then(response => {
            setPersons(response.users);
            setTotalPersons(response.count);
        });
    }, []);



    const classes = useStyles()

    const [persons, setPersons] = useState([])
    const [totalPersons, setTotalPersons] = useState(0)
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);


    const [open, setOpen] = useState(false);
    const [openRestorePerson, setOpenRestorePerson] = useState(false);
    const [openDetailPerson, setOpenDetailPerson] = useState(false);
    const [openEditPerson, setOpenEditPerson] = useState(false);
    const [openCreatePerson, setOpenCreatePerson] = useState(false);
    const [person, setPersonSelected] = useState({})


    const handleChangePage = (event, newPage) => {
        setPage(newPage);
        let data = { start: (newPage * rowsPerPage), end: rowsPerPage, paginate: true }
        listUsers(data).then(response => {
            setPersons(response.users);
            setTotalPersons(response.count);
        });
    }
    const handleChangeRowsPerPage = event => {
        setRowsPerPage(+event.target.value);
        setPage(0);
        let data = { start: 0, end: event.target.value, paginate: true }
        listUsers(data).then(response => {
            setPersons(response.users);
            setTotalPersons(response.count);
        });
    }
    const onUpdateUsers = () => {
        listUsers().then(response => {
            setPersons(response.users);
            setTotalPersons(response.count);
        });
    }



    const onOpenDeletePerson = id => () => {
        setPersonSelected(id)
        setOpen(true);
    }

    const onOpenRestorePerson = id => () => {
        setPersonSelected(id)
        setOpenRestorePerson(true);
    }

    const onCloseDeletePerson = () => {
        setOpen(false);
    }

    const onDeletePerson = () => {
        setOpen(false);

        deleteUser(person).then(response => {
            if (response.status === 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newPersons = persons.map((currentPerson) => {
                    if (person !== currentPerson._id)
                        return currentPerson

                    currentPerson.estado = "inactivo";
                    return currentPerson
                })
                setPersons(newPersons)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {

            toast.error('Ocurrió un error al desactivar el funcionario', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        })

    }

    const onRestorePerson = () => {

        setOpenRestorePerson(false)

        restoreUser(person).then(response => {
            if (response.status === 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newPersons = persons.map((currentPerson) => {
                    if (person !== currentPerson._id)
                        return currentPerson

                    currentPerson.estado = "activo";
                    return currentPerson
                })
                setPersons(newPersons)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {
            toast.error('Ocurrió un error al activar el funcionario', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        })
    }

    const onDetailPerson = person => () => {
        setPersonSelected(person)
        setOpenDetailPerson(true)
    }

    const closeApu = () => {
        setOpenDetailPerson(false)
    }

    const onEditPerson = person => () => {
        setPersonSelected(person)
        setOpenEditPerson(true)
    }

    const onCloseEditPerson = () => {
        setOpenEditPerson(false)
    }

    const onCloseRestorePerson = () => {
        setOpenRestorePerson(false)
    }

    const onCreatePerson = () => {
        setOpenCreatePerson(true)
    }

    const onCloseCreatePerson = () => {
        setOpenCreatePerson(false)
    }

    const onCloseModal = () => {
        setOpenCreatePerson(false)
    }


    return (
        <Fragment>
            <ToastContainer />
            <Paper className={classes.paper}>
                <Fab onClick={onCreatePerson} variant="extended" color="primary" size="small" className={classes.margin}>
                    <AddIcon className={classes.extendedIcon} />
                    Nuevo funcionario
                </Fab>

                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>FUNCIONARIO</TableCell>
                            <TableCell>DOCUMENTO</TableCell>
                            <TableCell>NOMBRE DE USUARIO</TableCell>
                            <TableCell>ROL</TableCell>
                            <TableCell>FOTO</TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            persons.map(item => {

                                return (
                                    <TableRow key={item._id}>
                                        <TableCell>{item.name} </TableCell>
                                        <TableCell>{item.document_number}</TableCell>
                                        <TableCell>{item.username}</TableCell>
                                        <TableCell>{item.role.name}</TableCell>
                                        <TableCell>{item.username}</TableCell>


                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Detalle" placement="top">
                                                <SearchIcon className={classes.icon} onClick={onDetailPerson(item)} />
                                            </Tooltip>
                                        </TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Editar" placement="top">
                                                <EditIcon className={classes.icon} onClick={onEditPerson(item)} />
                                            </Tooltip>
                                        </TableCell>
                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title={item.status} placement="top">
                                                {
                                                    item.status === 'activo' ?
                                                        <DeleteIcon className={classes.icon} onClick={onOpenDeletePerson(item._id)} />
                                                        :
                                                        <RestoreIcon className={classes.icon} onClick={onOpenRestorePerson(item._id)} />
                                                }
                                            </Tooltip>
                                        </TableCell>
                                    </TableRow>)
                            }
                            )}
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalPersons}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>


            <Dialog
                open={open}
                onClose={onCloseDeletePerson}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de eliminar este funcionario?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeletePerson} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDeletePerson} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={openRestorePerson}
                onClose={onCloseRestorePerson}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de Reactivar a esta persona?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseRestorePerson} color="primary">
                        NO
                    </Button>
                    <Button onClick={onRestorePerson} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog open={openDetailPerson} onClose={closeApu} TransitionComponent={Transition}>
                <DialogContent>
                    <PersonDetail person={person} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={closeApu} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog maxWidth="md" open={openEditPerson} onClose={onCloseEditPerson} TransitionComponent={Transition}>
                <DialogContent>
                    <PersonForm onCloseModal={onCloseModal} onUpdateUsers={onUpdateUsers} person={person} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseEditPerson} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                fullWidth={true}
                maxWidth="md"
                open={openCreatePerson}>
                <DialogTitle onClose={onCloseCreatePerson} >Crear usuario</DialogTitle>

                <DialogContent>
                    <PersonForm handleClose={onCloseCreatePerson} onCloseModal={onCloseModal} onUpdateUsers={onUpdateUsers} modal={true} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={onCloseCreatePerson} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>
        </Fragment>)
}

export default Person